import React, { Component } from 'react'
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { logout } from "../../actions/securityActions";
import 'jquery';
import 'bootstrap/dist/js/bootstrap';
import { Button, Navbar, Nav, NavDropdown, Form, FormControl } from 'react-bootstrap';

class Header extends Component {
    logout() {
        this.props.logout();
        window.location.href = "/";
    }
    render() {
        const { validToken } = this.props.security;
        const { userData } = this.props.userData;
        return (
            <Navbar className="navbar navbar-expand-lg navbar-dark mb-5" style={{ backgroundColor: "#003554" }} expand="lg">
                <div className="container">
                    <Navbar.Brand href="/">PlanApp - Free Time Planner</Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    {validToken && userData ? (
                        <Navbar.Collapse id="basic-navbar-nav">
                            <Nav className="mr-auto">
                                <Nav.Link href="/dashboard">Dashboard</Nav.Link>
                                <Nav.Link href="/user-info">{userData.firstname} {userData.lastname}</Nav.Link>
                                <Nav.Link href="/logout">Logout</Nav.Link>
                            </Nav>
                        </Navbar.Collapse>
                    ) : (
                            <Navbar.Collapse id="basic-navbar-nav">
                                <Nav className="mr-auto">
                                    <Nav.Link href="/register">Register</Nav.Link>
                                    <Nav.Link href="/login">Login</Nav.Link>
                                </Nav>
                            </Navbar.Collapse>
                        )}
                </div>
            </Navbar>
        )
    }
}

Header.propTypes = {
    logout: PropTypes.func.isRequired,
    security: PropTypes.object.isRequired,
    userData: PropTypes.object.isRequired
}

const mapStateToProps = state => ({
    security: state.security,
    userData: state.userData
})

export default connect(mapStateToProps, { logout })(Header);